<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list v-show="IsIncidentView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="fsm_incident_type"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="ID"
            :HeaderText="$t('IncidentType')" :listType="['List']" :ListData="IncidentListing" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <showReportIncidentModal v-if="showReportIncidentModal" :IncidentTypeId="IncidentId"></showReportIncidentModal>
        <SubIncidentModal v-if="showSubIncidentModal" :IncidentTypeId="IncidentId"></SubIncidentModal>
    </section>
</template>
<script>
import Schema from "@/views/IncidentType/IncidentTypeSchema";
import DataServices from "@/services/DataService";
import showReportIncidentModal from '../../components/Modal/showReportIncidentModal.vue';
import SubIncidentModal from '../../components/Modal/showReportSubIncidentModal.vue';
export default {
    components: { showReportIncidentModal, SubIncidentModal },
    data() {
        return {
            isLoading:false,
            showReportSubIncidentModal: false,
            showReportIncidentModal: false,
            viewIncidentModal: false,
            IncidentId: 0,
            showExpanded: '',
            StatusMappingList: [],
            noRecords: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "TYPE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Incident Type Type",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "DESCRIPTION",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "DESCRIPTION",
                "DISPLAY_ORDER": 3,
            }
            ],
            SubHeaders: [{
                "COLUMN_NAME": "TYPE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Sub Incident Type Type",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "DESCRIPTION",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "DESCRIPTION",
                "DISPLAY_ORDER": 3,
            }
            ],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            IncidentListing: [],
            SubIncidentListing: [],
            GroupStart: 1,
            GroupEnd: 3,
            ViewIncident: '',
            showSubIncidentModal: false,
            user_type: null,
            user_id: null,
            TypesubtpeDescription: '',
            IncidentType: '',
            ParentIncidentTypeName: '',
            ParentSubIncidentTypeName: '',
            CreationDate: '',
            IsIncidentSearch: false,
            IsSubIncidentSearch: false,
            IsDateSearch: false,
            IsSearchTrueOrFalse: false,
            SubIncidentShowHide: false,
            conditionForInstantSearch: '',
            SearchCondition: '',
            IsIncidentView: true
        }

    },
    created: async function () {
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchRecords();
    },
    methods: {
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event,isChild) {            
            switch (callbackfunction) {
                case 'DeleteIncident':
                    this.DeleteIncidentRow(item.ID);
                    break;
                case 'openReportIncidentPopUp':
                    this.openReportIncidentPopUp(item.ID,isChild);
                    break;
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        leftCommonSearch: function (value) {
            this.searchCondition = value.searchCondition;
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('incidentType')) {
                this.ParentIncidentTypeName = params['incidentType'];
            } else {
                this.ParentIncidentTypeName = ''
            }
            if (params.hasOwnProperty('subincidentType')) {
                this.ParentSubIncidentTypeName = params['subincidentType'];
            }
            else {
                this.ParentSubIncidentTypeName = ''
            }
            if (params.hasOwnProperty('creationDate')) {
                this.CreationDate = params['creationDate'];
            }
            else {
                this.CreationDate = ''
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteIncident();
                    break;
                case "openaddIncidentType":
                    this.openReportIncidentPopUp(null);
                    break;
                case "openaddSubIncidentType":
                    this.openSubIncidentPopUp();
                    break;
            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        formatDateTime: function (value) {
            if (value) {
                return moment(value).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(Globalize.culture().calendar.patterns.t).replace("t", "A").replace("t", ""))
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        onEnter: function (e) {
            var vm = this;
            if (e.charCode == 13) {
                e.preventDefault();
                vm.SearchCondition = vm.SearchCondition;
                vm.CurrentPage = 1;
                vm.PageSize = 10;
                vm.PageNumber = 1;
                vm.FetchRecords();
            }
        },
        Searchdata: function () {
            var vm = this;
            vm.SearchCondition = vm.SearchCondition;
            vm.FetchRecords();
        },
        refreshList: function () {
            this.SearchCondition = "";
            this.ParentIncidentTypeName = "";
            this.ParentSubIncidentTypeName = "";
            this.CreationDate = "";
            this.IsSearchTrueOrFalse = false;
            this.FetchRecords();
        },
        DeleteIncident: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("IncidentType")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_INCIDENT_TYPE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.status == 200) {                           
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('IncidentType')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('IncidentType')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('IncidentType')]), "failure", true, vm.$t('Alert'));
            }
        },
        openReportIncidentPopUp: function (id,checkIsChild) {  
            var vueObj = this;
            if (id == undefined) {
                id = 0;
            }
            if(checkIsChild){
                this.openSubIncidentPopUp(id);
            }
            else{
                vueObj.IncidentId = id;
                this.showReportIncidentModal = !this.showReportIncidentModal;
            }
        },
        openSubIncidentPopUp: function (id) {    
            var vueObj = this;
            if (id == undefined) {
                id = 0;
            }
            vueObj.IncidentId = id;
            vueObj.showSubIncidentModal = true;
        },
        DeleteIncidentRow: function (id) {            
            var vm = this;
            var val = id;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("IncidentType")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_INCIDENT_TYPE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('IncidentType')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('IncidentType')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                // ShowAlert(Globalize.localize("Selectanyrecordtodelete"), "warning", true, Globalize.localize("Alert"));
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },        
        searchFunction: function (event) {
            var vueObj = this;
            vueObj.IsIncidentSearch = false;
            vueObj.IsSubIncidentSearch = false;
            vueObj.IsSearchTrueOrFalse = true;
            vueObj.IsDateSearch = false;
            if ($.trim(vueObj.ParentIncidentTypeName) != "") {
                vueObj.IsIncidentSearch = true;
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.conditionForInstantSearch = "";
                vueObj.conditionForInstantSearch = vueObj.ParentIncidentTypeName;
            }
            if ($.trim(vueObj.ParentSubIncidentTypeName) != "") {
                vueObj.IsSubIncidentSearch = true;
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.conditionForInstantSearch = "";
                vueObj.conditionForInstantSearch = vueObj.ParentSubIncidentTypeName;
            }
            if ($.trim(vueObj.CreationDate) != "") {
                vueObj.IsDateSearch = true;
                vueObj.IsSearchTrueOrFalse = true;
                vueObj.conditionForInstantSearch = vueObj.conditionForInstantSearch != "" ? " AND " : "";
                vueObj.conditionForInstantSearch = vueObj.CreationDate;
            }
            vueObj.CurrentPage = 1;
            vueObj.PageSize = "";
            if (event) {
                vueObj.PageNumber = 1;
                vueObj.GroupStart = 1;
                vueObj.GroupEnd = 3;
            }
            vueObj.FetchRecords();
        },
        onExpandChild: function (item) {            
            var vueObj = this;  
            vueObj.isLoading=true;         
            var url = `ParentID=${item.ID}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&incidentType=${vueObj.ParentIncidentTypeName}&subincidentType=${vueObj.ParentSubIncidentTypeName}&creationDate=${vueObj.CreationDate}`;
            DataServices.GetFsmIncidentListing(url)
                .then(function (response) {                    
                    if (response.data != "" && response.data != null) {
                        if (response.data.status == "Success") { 
                            if (response.data.result.DATA.length > 0) {
                                vueObj.Headers = vueObj.SubHeaders;
                                item.child.data = response.data.result.DATA;
                                $.each(item.child.data, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {                                    
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });                                
                                item.selectedForm = forms;
                                });
                                vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.CurrentPage = vueObj.PageNumber;
                                vueObj.listheaderbuttons[2].isdisabled = true;
                                vueObj.noRecord = false;
                                vueObj.isLoading=false;  
                            }
                            else {
                                vueObj.noRecord = true;
                                item.child.data = [];
                                vueObj.TotalRecords = 0;
                                vueObj.isLoading=false; 
                            }
                        }
                        else {
                                vueObj.noRecord = true;
                                item.child.data = [];
                                vueObj.TotalRecords = 0;
                                vueObj.isLoading=false; 
                            }
                    }
                    else {
                        vueObj.noRecord = true;
                        item.child.data = [];                        
                        vueObj.TotalRecords = 0;
                        vueObj.isLoading=false; 
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        },
        FetchRecords:async function () {
            var vueObj = this;
            var url = `ParentID=&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&incidentType=${vueObj.ParentIncidentTypeName}&subincidentType=${vueObj.ParentSubIncidentTypeName}&creationDate=${vueObj.CreationDate}`;
            await DataServices.GetFsmIncidentListing(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.status == "Success") {
                            var updateArray = response.data.result.DATA
                            updateArray.forEach((x) => {
                                x.child = {
                                    onExpandChild: vueObj.onExpandChild,
                                    isShowChild: false,
                                    isShowExpandButton: true,
                                    data: []                                    
                                }
                            });   
                            $.each(updateArray, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {                                    
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });                                
                                item.selectedForm = forms;
                            });                        
                            vueObj.IncidentListing = updateArray;
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[2].isdisabled = true;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.IncidentListing = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.IncidentListing = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }                  
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        }      
    }
}
</script>