<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ManageSubIncident') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditSubIncidentType')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import DataServices from "@/services/DataService";
export default {
    props: {
        IncidentTypeId: {
            type: Number,
            required: true
        },
        ticketId:{
            required:false
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('IncidentType'),
                            name: "IncidentType",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ParentIncidentType'),
                            name: "ParentIncidentType",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            validationRules: { "required": true,}
                        },
                        {
                            astype: "TextField",
                            label: this.$t('DESCRIPTION'),
                            name: "DESCRIPTION",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vueObj = this;
        if(vueObj.ticketId>0){
        vueObj.GetDDLData('FSM_INCIDENT', vueObj.ticketId);
        }else{
        vueObj.GetDDLData('FSM_INCIDENT',0);
        }
        if(vueObj.IncidentTypeId>0){
            this.FetchData();
        }
        
    },
    methods: {
        GetDDLData:async function(value,ticketId){ 
            var vm=this;  
            if(ticketId>0){
                var incidentTypeId=ticketId;
            }else{
            var incidentTypeId=vm.IncidentTypeId==undefined?0:vm.IncidentTypeId;
          }     
            var appendingUrl = `id=${incidentTypeId}&moduleName=FSM&type=${value}`;
            DataServices.GetDDLDataFSM(appendingUrl).then(response=>{
                this.FormSchema[0].Data[1].config.options=response.data.result.DATA;
            })
        },
        FetchData: function () {
            var vueObj = this;
            var url = `Id=${vueObj.IncidentTypeId}`;
            DataServices.FetchDataByIncidentId(url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.result.DATA[0].TYPE_NAME;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.result.DATA[0].PARENT_ID;
                            (vueObj.FormSchema)[0].Data[2].value = response.data.result.DATA[0].DESCRIPTION;
                        }
                    }
                }
            );
        },
        Close: function () {            
            this.$emit("close");
            this.$parent.showSubIncidentModal = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                IncidentId: vm.IncidentTypeId == null ? 0 : vm.IncidentTypeId,
                IncidentName: formfields.IncidentType,
                ParentIncidentId: formfields.ParentIncidentType,
                ModuleName: 'FSM',
                StatusId: 1001,
                Description: formfields.DESCRIPTION
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveIncidentType(params).then((response) => {               
                if (response.data.status == 'Success') {
                    if (vm.IncidentTypeId > 0) {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("IncidentType")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    } else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("IncidentType")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    vm.$parent.FetchRecords();
                } else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>